.nav-bar{
  display: flex;
  flex-direction: row;
  font-size: larger;
  justify-content: space-evenly;
  padding-top: 25px;
  padding-bottom: 30px;
  border-style: none none solid none;
}

.main{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'IBM Plex Serif';
}
.main > button, input{
  width: 100%;
}
.main > form{
  padding-top: 20px;
  padding-bottom: 20px;
}

.grid{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(10, 1fr);
  width: 100%;
  grid-gap: 1em;
  padding-top: 30px;
}

.cell{
  grid-column: span 3;
  grid-row: span 2;
  border: solid 2px;
}
button{
  font-size: larger;
}